@import "../../variables";

.contact-form {
  background: #f8f4e5;
  padding: 50px 100px;
  border: 2px solid $altbackground;
  box-shadow: 15px 15px 1px $form-shadow, 15px 15px 1px 2px $altbackground;
}

.input {
  display: block;
  width: 100%;
  font-size: $font-size;
  line-height: $font-size * 2;
  font-family: MomcakeBold;
  margin-bottom: $font-size * 2;
  border: none;
  border-bottom: 5px solid $altbackground;
  background: $form-bg;
  min-width: 250px;
  padding-left: 5px;
  outline: none;
  color: $altbackground;
}

.input:focus {
  border-bottom: 5px solid $form-shadow
}
.contact-btn {
  display: block;
  margin: 0 auto;
  line-height: $font-size * 2;
  padding: 0 20px;
  background: $form-shadow;
  font-family: MomcakeBold;
  letter-spacing: 2px;
  transition: .2s all ease-in-out;
  outline: none;
  border: 1px solid $altbackground;
  box-shadow: 3px 3px 1px $form-primary-highlight, 3px 3px 1px 1px $altbackground;
}
  .contact-btn:hover {
    background: $altbackground;
    color: white;
    border: 1px solid $altbackground;
  }
.contact-btn::selection {
  background: $form-secondary-highlight
}

.input:-webkit-autofill,
.input:-webkit-autofill:hover, 
.input:-webkit-autofill:focus {
  border-bottom: 5px solid $form-primary-highlight;
  -webkit-text-fill-color: $font-color;
  -webkit-box-shadow: 0 0 0px 1000px $form-bg inset;
  transition: background-color 5000s ease-in-out 0s;
}